<!--
 * @Description: 人才主页
 -->
 <template>
  <div id="talent-default">
    <!--左边长列表-->
    <div class="box1-left">
      <currentLocate></currentLocate>
      <!-- 职位搜索 -->
      <div class="box2-left">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">职位搜索</a>
          </li>
        </ul>
        <div style="width:100%;">
          <keep-alive>
            <job-search></job-search>
          </keep-alive>
        </div>
      </div>
      <div class="box3-left" style="height:312px;">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('最新招聘')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('专家库')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('高校')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('专家智点')" :widgetType=2></pb-list>
        </keep-alive>
      </div>
      <!-- </div> -->
    </div>
    <!--右边长列表-->
    <div class="box4-right">
      <div class="box6-right">
        <user-box></user-box>
      </div>
      <div class="box7-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs2[0]" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <div class="box12-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="title-active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>
 <script>
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import currentLocate from "@/components/common/tools/currentLocate";
import JobSearch from "@/components/common/tools/job-search";
import PbList from "@/components/common/tools/pb-list";
import { proofreading } from "@/api/getData";

export default {
  name: "talent-default",
  data() {
    return {
      tabs: global.tabs
    };
  },
  filters: {
    FormatDate: function(value, type) {
      if (type) {
        value = value.filter(item => {
          if (item.name == type) return true;
        });
      }
      return value;
    }
  },
  computed: {
    tabs2() {
      let value = global.tabs;
      let value1, value2;
      value1 = value.filter(item => {
        if (item.belong == "活动") return true;
      });
      if (!value[0].message && !value[0].message && !value[0].message) {
        proofreading.call(this); //数据未加载，调用函数校对加载
      }
      value = [value1];
      return value;
    }
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "人才"
      );
      global.currentPath.data = [index];
    }
  },
  activated() {
    this.init();
  },
  components: {
    UserBox,
    slide2,
    PbList,
    currentLocate,
    JobSearch
  }
};
</script>
<style lang="scss" scoped>
$blue: #127de7;
#talent-default /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#talent-default {
  @include adv;
  height: 100%;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 12px;
}
.hot_search {
  @include adv;
  height: 100%;
  //margin-left:15px;
  padding: 13px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  margin-top: 20px;
  width: 790px;
  float: left;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}

.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/*左边小部件样式*/
.box3-left {
  @include i-box;
  min-height: 262px;
  margin-top: 12px;
  background: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.box2-left {
  & {
    @include i-box;
    @include border;
    background: rgba(255, 255, 255, 0.95);
    margin-top: 20px;
  }
  .title {
    margin-bottom: 12px;
    margin-top: 2px;
    width: 100%;
    height: 28px;
    line-height: 28px;
    background: none;
  }
  .title li {
    float: left;
  }
  .more {
    & {
      color: #666;
      font-size: 14px;
      float: right;
      padding: 2px 10px 0 2px;
      cursor: pointer;
    }
    &:hover {
      color: $blue;
    }
  }
  .list_title a {
    cursor: pointer;
    color: #7b7b7b;
    font-size: 1em;
    font-weight: 500;
    line-height: 25px;
    vertical-align: middle;
    margin-right: 10px;
    color: $blue;
    border-bottom: 2px solid $blue;
    padding-bottom: 4px;
  }
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box12-right,
.box5-right,
.box6-right,
.box7-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
</style>



